import { Box, Paper, Typography, Grid, Button } from "@mui/material";
import { FC, useEffect } from "react";
import { useForm, FieldValues } from "react-hook-form";
import AppTextInput from "../../app/components/app-textinput";
import { Product } from "../../app/models/product";
import useProducts from "../../app/hooks/useProducts";
import AppSelectList from "../../app/components/app-selectlist";
import AppDropZone from "../../app/components/app-dropzone";
import { yupResolver } from '@hookform/resolvers/yup'; 
import { validationSchema } from './product-validation';
import agent from '../../app/api/agent';
import { useAppDispatch } from '../../app/store/configure-store';
import { setProduct } from "../catalog/catalog-slice";
import { LoadingButton } from '@mui/lab';

interface ProductFormProps {
  product?: Product;
  cancelEdit: () => void;
}

const ProductForm: FC<ProductFormProps> = ({ product, cancelEdit }) => {
  const { control, reset, handleSubmit, watch, formState: {isDirty, isSubmitting} } = useForm({
    resolver: yupResolver(validationSchema)
  });
  const { brands, types } = useProducts();
  const watchFile = watch("file", null);
  const dispatch = useAppDispatch(); 

  useEffect(() => {
    if (product && !watchFile && !isDirty) reset(product);
    
    //this clean up function is called when component is unmounted
    return () => {
        if(watchFile) URL.revokeObjectURL(watchFile.preview);
    }
  }, [product, reset, watchFile, isDirty]);

  const handleSubmitData = async (data: FieldValues) => {
    try {
        let response: Product;
        if(product) {
            response = await agent.Admin.updateProduct(data);
        } else {
            response = await agent.Admin.createProduct(data);
        }

        dispatch(setProduct(response));
        cancelEdit();

    } catch (error) {
        console.log(error);
    }
  };

  return (
    <Box component={Paper} sx={{ p: 4 }}>
      <Typography variant="h4" gutterBottom sx={{ mb: 4 }}>
        Product Details
      </Typography>
      <form onSubmit={handleSubmit(handleSubmitData)}>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={12}>
            <AppTextInput control={control} name="name" label="Product name" />
          </Grid>
          <Grid item xs={12} sm={6}>
            <AppSelectList
              control={control}
              name="brand"
              label="Brand"
              items={brands}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <AppSelectList
              control={control}
              name="type"
              label="Type"
              items={types}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <AppTextInput
              control={control}
              type="number"
              name="price"
              label="Price"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <AppTextInput
              control={control}
              type="number"
              name="quantityInStock"
              label="Quantity in Stock"
            />
          </Grid>
          <Grid item xs={12}>
            <AppTextInput
              control={control}
              multiline={true}
              rows={4}
              name="description"
              label="Description"
            />
          </Grid>
          <Grid item xs={12}>
            <Box display="flex" justifyContent="space-between" alignItems="center" >
                <AppDropZone control={control} name="file" />
                { watchFile ? (
                    <img src={watchFile.preview} alt="preview" style={{ maxHeight: 200}} />
                ) : (
                    <img src={product?.pictureUrl} alt={product?.name} style={{ maxHeight: 200}} />
                )}
            </Box>
          </Grid>
        </Grid>
        <Box display="flex" justifyContent="space-between" sx={{ mt: 3 }}>
          <Button variant="contained" color="inherit" onClick={cancelEdit}>
            Cancel
          </Button>
          <LoadingButton loading={isSubmitting} type="submit" variant="contained" color="success">
            Submit
          </LoadingButton>
        </Box>
      </form>
    </Box>
  );
};

export default ProductForm;
